
import { getCaptcha } from '@/apis/auth';
import Vue from 'vue'
export default Vue.extend({
    props: {
        callback: Function,
    },
    data() {
        return {
            image: '',
        }
    },
    mounted() {
        this.changeCaptcha();
    },
    methods: {
        async changeCaptcha() {
            const { data, hash } = await getCaptcha();
            this.image = data;
            this.$emit('callback', hash);
        }
    }
})
